import React from 'react';
import './card.scss';
import close from './../../assets/close.png';

function Card({ children, deleteOnecard, isImg }) {

    const deleteOnelist = () => {
        deleteOnecard()
    }

    return (
        <div className="card">
            <div>{children}</div>
            {isImg ? <img onClick={deleteOnelist} src={close} alt="close" /> : ''}
        </div>
    )
}

export default Card;
