const initial = {
    items:  {
        0: {
            name: 'Задачи на месяц', 
            item: [
                {id: 0, name: "Английский", done: false},
                {id: 1, name: "спорт", done: false},
                {id: 2, name: "Программирование", done: false},
                {id: 3, name: "медитация", done: false}
            ]
        },
        1: {
            name: 'Задачи на день', 
            item: [
                {id: 0, name: "турник", done: true},
                {id: 1, name: "анжуманья", done: true},
                {id: 2, name: "прэс качат", done: false},
                {id: 3, name: "бег", done: true},
                {id: 4, name: "пратеин купит", done: false}
            ]
        }  
    }
}


const penal = (state = initial, action) => {
    switch (action.type) {
        case 'ADD_CARD':
            const newword = {id: Math.random() * 4, name: action.text, done: false};
            const newItem = [...state.items[action.el].item, newword];
            console.log(newItem)
            return {
                ...state,
                items: {
                    ...state.items,
                    [action.el]: {
                        ...state.items[action.el],
                        item: newItem
                    }
                }
            }
        case 'ADD_NEW_LIST':
            return {
                ...state,
                items: {...state.items, [action.newName]: action.obj}
            }
        case 'DELETE_ALL_CARD':
            const newItems = {
                ...state.items
            }
            delete newItems[action.el]
            return {
                ...state,
                items: newItems
            }
        case 'ADD_NEW_NAME':
            const newItems2 = {
                ...state.items
            }
            newItems2[action.element].name = action.newName
            return {
                ...state,
                items: newItems2
            }
        case 'DELETE_ONE_LIST_OF_CARD':
            const newItem2 = [...state.items[action.el].item];
            return {
                ...state,
                items: {
                    ...state.items,
                    [action.el]: {
                        ...state.items[action.el],
                        item: newItem2.filter(el => el.id !== action.id)
                    }
                }
            }
        case 'CHENGE_DONE':
            const newitem = [...state.items[action.penalname].item].filter(el => {
                if(el.id === action.id) {
                    return {
                        ...el,
                        done: el.done = !el.done
                    }
                } else {
                    return {el}
                }
            })
            return {
                ...state,
                items: {
                    ...state.items,
                    [action.penalname]: {
                        ...state.items[action.penalname],
                        item: newitem
                    }
                }
            }

            // const newItems4 = {...state.items};
            // const newIArr = newItems4[action.penalname].item.map(el => {return {...el}});
            // newIArr[action.id].done = !newIArr[action.id].done;
            // newItems4[action.penalname].item = newIArr;
            // console.log(newItems4)
            return {
                ...state
            }
        default:
           return state;
    }
};

export const addNewCardItem = (el, text) => {
    return {
        type: "ADD_CARD", el, text
    }
};

export const addNewListItem = (obj, newName) => {
    return {
        type: "ADD_NEW_LIST", obj, newName
    }
}

export const dalateAllItem = (el) => {
    return {
        type: "DELETE_ALL_CARD", el
    }
}

export const addNewName = (element, newName) => {
    return {
        type: "ADD_NEW_NAME", element, newName
    }
}

export const deleteOneListofcard = (el, id) => {
    return {
        type: "DELETE_ONE_LIST_OF_CARD", el, id
    }
}

export const taskDoneAC = (penalname, id) => {
    return {
        type: "CHENGE_DONE", penalname, id
    }
}

export default penal;